import {
  Grid,
  GridItem,
  Image,
  Button,
  IconButton,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  useColorMode,
  useColorModeValue,
  LightMode,
  Stack,
  Slide,
} from "@chakra-ui/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShoppingCart } from "@fortawesome/free-solid-svg-icons";
import { Link as ReactLink, NavLink } from "react-router-dom";
import BadgeButton from "../utils/BadgeButton";
import * as React from "react";
import { HamburgerIcon, MoonIcon, SunIcon } from "@chakra-ui/icons";
import DividerHelper from "../utils/DividerHelper";
import { darkModeBackgroundColor } from "../utils/Helper";

const NavMenu = [
  { name: "Home", path: "/" },
  { name: "Category", path: "/category" },
  { name: "Latest", path: "/latest" },
  { name: "Blog", path: "/blog" },
  { name: "Contact", path: "/contact" },
];

const Navbar = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();
  const { colorMode, toggleColorMode } = useColorMode();
  const [IsScroll, setIsScroll] = React.useState(false);

  window.addEventListener("scroll", () => {
    setIsScroll(window.scrollY >= 68 ? true : false); // Neu chinh xac thi thay 60 = height cua navbar
  });

  return (
    <Stack
      pos={IsScroll ? "fixed" : "initial"}
      zIndex="9999"
      background={useColorModeValue("white", darkModeBackgroundColor)}
      w="100%"
    >
      <Grid
        autoFlow="column dense"
        justifyContent="space-between"
        w="100%"
        py="5"
        px="10"
        alignItems="center"
      >
        <GridItem>
          <ReactLink to="/">
            <Image src="data:image/webp;base64," />
          </ReactLink>
        </GridItem>

        <Grid
          autoFlow="column"
          display={{ base: "none", md: "grid" }}
          justifyItems="end"
          gap="5"
          alignItems="center"
        >
          {NavMenu &&
            NavMenu.map((item, idx) => (
              <GridItem
                _hover={{ color: "pink.400", cursor: "pointer" }}
                transitionDuration="0.4s"
                fontSize="large"
                fontWeight="semibold"
                key={`nav-link-${idx}`}
              >
                <NavLink
                  exact
                  to={item.path}
                  activeStyle={{ color: "var(--chakra-colors-pink-400)" }}
                >
                  {item.name}
                </NavLink>
              </GridItem>
            ))}

          <IconButton
            onClick={toggleColorMode}
            variant="ghost"
            icon={colorMode === "light" ? <MoonIcon /> : <SunIcon />}
          />

          <GridItem>
            <NavLink to="/cart-detail">
              <BadgeButton
                counter={2}
                buttonIcon={<FontAwesomeIcon icon={faShoppingCart} />}
              />
            </NavLink>
          </GridItem>
          <GridItem>
            <LightMode>
              <Button borderRadius="40px" colorScheme="blue">
                Sign In
              </Button>
            </LightMode>
          </GridItem>
        </Grid>

        <GridItem display={{ md: "none" }}>
          <IconButton
            onClick={toggleColorMode}
            variant="ghost"
            icon={colorMode === "light" ? <MoonIcon /> : <SunIcon />}
          />
          <IconButton
            ref={btnRef}
            onClick={onOpen}
            icon={<HamburgerIcon boxSize="24px" />}
            aria-label="hamburger"
            variant="outline"
            // colorScheme="messenger"
          />
        </GridItem>

        <Drawer
          isOpen={isOpen}
          placement="right"
          onClose={onClose}
          finalFocusRef={btnRef}
        >
          <DrawerOverlay />
          <DrawerContent>
            <DrawerCloseButton />
            <DrawerHeader>Hi, Tan!</DrawerHeader>

            <DrawerBody p="0" mt="5">
              {NavMenu &&
                NavMenu.map((item, idx) => (
                  <NavLink
                    exact
                    to={item.path}
                    style={{ display: "block", padding: "20px 20px" }}
                    activeStyle={{
                      backgroundColor: "#7483e8",
                      color: "white",
                    }}
                    onClick={onClose}
                    key={`nav-link-${idx}`}
                  >
                    {item.name}
                  </NavLink>
                ))}
            </DrawerBody>

            <DrawerFooter>
              <ReactLink to="/cart-detail">
                <IconButton
                  variant="outline"
                  isRound
                  icon={<FontAwesomeIcon icon={faShoppingCart} />}
                  size="lg"
                />
              </ReactLink>
              {/* <BadgeButton counterProp={2} iconProp={faShoppingCart} /> */}
              <Button borderRadius="40px" colorScheme="blue">
                Sign In
              </Button>
            </DrawerFooter>
          </DrawerContent>
        </Drawer>
      </Grid>

      <DividerHelper />
    </Stack>
  );
};

export default Navbar;
